// Checkout

#checkout {
    padding: 2rem 0;
    .section-title {
        @include font-size(24px);
        color: $title;
        font-weight: 300;
        margin: 0; }
    .title {
        @include font-size(18px);
        color: $title;
        font-weight: 300;
        margin: 0;
        margin-bottom: 1rem; }
    .summary {
        .title {
            @include font-size(14px); }
        .totals {
            margin-top: 1rem;
            text-align: right;
            .price {
                @include font-size(16px);
                color: $green; }
            .place-order {
                .btn {
                    @include font-size(18px); } } } } }
