// Pagination

#pagination {
    .info {
        @include font-size(14px);
        color: $text; }
    .list {
        @include clear;
        float: right;
        list-style: none;
        .item {
            float: left;
            margin-right: .5rem;
            &:last-child {
                margin-right: 0; } } } }
