// General

*,
&::before,
&::after {
    box-sizing: border-box;
    margin: 0;
    outline: 0;
    padding: 0; }

html {
    font-size: $base-font-size; }

body {
    background: url('../img/bg.jpg') repeat;
    font-family: $sans; }

img {
    vertical-align: middle; }

.pull-right {
    float: right; }
